.hero {
  position: relative;
  padding-top: 110px;
  padding-bottom: 240px;
  background-color: var(--olive-color);
  background-image: url(../images/mobile-hero/hero-point.png);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 328px 50px;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/mobile-hero/hero-point@2x.png);
  }

  @media screen and (min-width: 768px) {
    padding-top: 104px;
    padding-bottom: 13px;
    background-image: url(../images/tablet-hero/point-hero.png);
    background-size: 767px 67px;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/tablet-hero/point-hero@2x.png);
    }
  }

  @media screen and (min-width: 1200px) {
    padding-top: 118px;
    padding-bottom: 65px;
    background-image: url(../images/desktop-hero/point-hero.png);
    background-size: 1200px 384px;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/desktop-hero/point-hero@2x.png);
    }
  }
}

.wrapper-content-title-shop {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
}

.hero__title {
  width: 250px;
  margin-bottom: 32px;
  font-family: 'Seymour One';
  font-style: normal;
  font-size: 26px;
  line-height: 1.27;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--main-white-color);

  @media screen and (min-width: 768px) {
    width: 181px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 1.27;
    letter-spacing: normal;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 28px;
    width: 367px;
    font-weight: 400;
    font-size: 38px;
    line-height: 1.26;
    letter-spacing: 0.04em;
  }

  &--big {
    @media screen and (max-width: 767px) {
      font-size: 34px;
      line-height: 1.03;
      letter-spacing: normal;
    }

    @media screen and (min-width: 768px) and (max-width: 1199px) {
      font-size: 24px;
      line-height: 0.54;
    }
  }
}

