.hero__list-shop {
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  @media screen and (min-width: 1200px) {
    gap: 16px;
  }
}

.hero__desc-shop {
  display: flex;
  flex-direction: column;
  gap: 4px;

  font-size: 8px;
  line-height: 1.25;
  text-align: right;
  color: var(--main-white-color);

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.21;
  }
}

.hero__numb {
  font-weight: 900;
  font-size: 28px;
  line-height: 1.21;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--main-black-color);
  @media screen and (min-width: 1200px) {
    font-size: 46px;
  }
}

.wrapper-content-img-ahchor {
  display: flex;
  gap: 33px;
  align-items: flex-end;

  @media screen and (min-width: 1200px) {
    gap: 14px;
    align-items: center;
  }
}

.hero__list-anchor {
  display: flex;
  gap: 8px;

  @media screen and (min-width: 768px) {
    margin-bottom: 28px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 42px;
    gap: 12px;
  }
}

.hero__anchor-about {
  font-weight: 600;
  font-size: 8px;
  line-height: 1.25;
  text-decoration-line: underline;
  color: var(--yello-color);
  transition: color 250ms linear;

  &:hover {
    color: var(--btn-hover-focus-color);
  }
  &:focus {
    color: var(--main-white-color);
  }

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.21;
  }
}

.hero__img-plate {
  display: none;

  @media screen and (min-width: 768px) {
    display: block;
    width: 152px;
    height: 152px;
  }

  @media screen and (min-width: 1200px) {
    width: 225px;
    height: 225px;
  }
}

.wrapper-content-desc {
  display: none;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 151px;
  }
  @media screen and(min-width: 1200px) {
    width: 200px;
    gap: 32px;
  }
}

.hero__desc {
  @media screen and (min-width: 768px) {
    font-size: 12px;
    line-height: 1.4;
    color: var(--main-white-color);
  }
  @media screen and(min-width: 1200px) {
    font-size: 14px;
    line-height: 1.41;
    padding-top: 40px;
  }
}

.hero__icon-arrow {
  fill: var(--main-white-color);
}

